import App from './App'
import routes from './router'
import store from './store'
import _ from 'lodash'

Vue.config.productionTip = false

Vue.prototype._ = _

Vue.use(VueRouter)

const router = new VueRouter({
  routes,
  mode: 'history'
})

new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



